.navibar {
    width: 97vw;
    height: 4rem;
    display: block;
    margin: 0 auto;
    position: sticky;
    top: 5px;
    z-index: 3;
    background: white;
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 5px 5px rgba(0, 0, 0, 0.2);
}

.navibar>ul  {
    height: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    
}

.navibar ul>li {
    display: inline-flex;
    align-items: center;
    justify-content: center;

    height: 100%;
    width: 100%;

    background-color: aquas;
}

  /* Style the active class, and buttons on mouse-over */
.navibar ul>li:hover {
    background-color: rgba(0, 123, 255, 0.2);
    font-size: 105%;
}
  
/* Style the active class*/
.active{
    background-color: rgba(94, 172, 255, 0.3);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
